.progress {
  --progress: 0%;
  background: #333;
  border: 1px solid #fff;
  width: 100%;
  height: 30px;
  margin: 20px auto;
  padding: 3px;
  box-shadow: 0 0 10px #aaa;
}

.progress .bar {
  width: var(--progress);
  background: linear-gradient(#add8e6, #6495ed, #add8e6);
  height: 100%;
  transition: width 3s;
  animation: 4s ease-in infinite shine, 1s ease-out end;
  box-shadow: 0 0 10px #789;
}

:root {
  --progress: 0%;
}
